.App {
  text-align: center;
}

.App-logo {
  height: 40px;
  margin: 20px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  color: #fff;
  text-decoration: none;
}

a.active {
  color: #fbb10a;
  text-shadow: #fbb10a 0 0 10px;
}

ul.header {
  position: fixed;
  top: 0;
  width: 100%;
  backdrop-filter: blur(5px);
  background: -moz-linear-gradient(bottom, #2A343Dd8, #2A343Df0, #2A343DFF);
  background: -webkit-linear-gradient(bottom, #2A343Dd8, #2A343Df0, #2A343DFF);
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-center {
  text-align: center;
}