.server {
  border: 3px solid #fff3;
  border-radius: 54px;
  margin-bottom: 10px;
  text-align: start;
  display: flex;
  align-items: flex-start;
  min-height: 102px;

  &:hover {
    background-color: #ffffff08;

    .server-image {
      background-position: right center;
    }
  }

  .info-plate {
    width: calc(100% - 84px);
    min-height: 108px;
    display: flex;
    align-items: center;
  }
}

@media (min-width: 601px) {
  .server {
    backdrop-filter: blur(10px) brightness(50%) !important;
    background: none !important;
    background: -moz-linear-gradient(#000000c0, #000000b0, #000000c0) !important;
  }
}

.server-mods-details-content {
    font-size: 10px;
    background: #000a;
    border: 2px solid #fffa;
    border-radius: 5px;
    margin: 10px;
    padding: 5px;
    overflow-y: scroll;
    max-height: 60px;
    height: min-content;
  }
  
  @media only screen and (min-width: 601px) {
    .server-mods-details-content {
      font-size: 13px;
      max-height: 90px;
    }
  }
  
  @media only screen and (min-width: 1270px) {
    .server-mods-details-content {
      font-size: 16px;
      max-height: 120px;
    }
  }

.server-join {
  display: none;
}

@media (min-width: 601px) {
  .server-join {
    display: flex;
    align-self: center;

    .join-button {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin-right: 26px;
    }   
}
}

.server-image-wrapper {
  display: none;
}

@media (min-width: 601px) {
  .server-image-wrapper {
    height: 102px;
    width: 102px;
    min-height: 102px;
    min-width: 102px;
    margin: 2px; 
    display: block;   
  }
}

.server-image {
  border-radius: 50px;
  height: 100%;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: cover;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: 0.25s;
  text-align: center;
  /* will-change: transform; */
  display: flex; 
  align-items: center; 
  justify-content: center;
}

.server-info {
  margin: 10px 10px 10px 35px;
  width: 60%;
  max-width: 60%;
  /* font-size: calc(6px + 2vmin); */
}

@media screen and (min-width: 601px) {
  .server-info {
    margin: 10px 10px;
    width: 60%;
    max-width: 60%;
    /* font-size: calc(6px + 2vmin); */
  }
}

.server-info-server-name {
  cursor: default;
  // text-overflow: ellipsis;
  overflow: hidden;
  // white-space: nowrap;
  font-size: 16px;
}

@media only screen and (min-width: 601px) {
  .server-info-server-name {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1270px) {
  .server-info-server-name {
    font-size: 25px;
  }
}

.server-info-server-params, .server-info-server-mods, .server-started {
  cursor: default;
  font-size: 12px;
  color: #fffc;
}

@media only screen and (min-width: 601px) {
  .server-info-server-params, .server-info-server-mods, .server-started {
    font-size: 15px;
  }
}

@media only screen and (min-width: 1270px) {
  .server-info-server-params, .server-info-server-mods, .server-started {
    font-size: 18px;
  }
}

.server-utilization {
  cursor: default;
  margin: 10px 10px;
  width: 10%;
  font-size: 12px;
}

@media only screen and (min-width: 601) {
  .server-utilization {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1270px) {
  .server-utilization {
    font-size: 25px;
  }
}

.server-location {
  cursor: default;
  margin: 10px 10px;
  width: 25%;
  font-size: 12px;
}

@media only screen and (min-width: 601) {
  .server-location {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1270px) {
  .server-location {
    font-size: 25px;
  }
}

.server-flag-image {
  margin: 0 10px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  /* max-width: 30px; */
}