.modal {
    position: fixed;
    background-color: #0009;
    background: linear-gradient(#0006, #0009, #0006);
    background: -moz-linear-gradient(#000000e0, #000000f0, #000000e0);
    opacity: 0;
    pointer-events: none;
    display: grid;
    justify-content: center;
    align-items: center;
    inset: 0;
    z-index: 100;
    transition-property: all;
    transition-timing-function: cubic-bezier(.4,0,.2,1);
    transition-duration: .15s;
  
    &.open {
        opacity: 1;
        backdrop-filter: blur(10px);
        pointer-events: auto;
    }
  }

button:disabled {
    color: #ccca;
    border-color: #ccca;
    cursor: not-allowed;

    &:hover {
        color: #ccca;
        text-shadow: none;
        border-color: #ccca;
    }
}